import React from "react";
import Link from "next/link";

export default function TopNavigation() {
  return (
    <nav className="w-full flex items-center justify-between px-6 py-3 bg-gradient-to-r from-black via-purple-950 to-black backdrop-blur-xl shadow-2xl sticky top-0 z-50 border-b border-purple-500/30">
      <div className="flex items-center gap-3">
        {/* Logo and placeholder removed as requested */}
        <Link href="/" className="flex items-center gap-2 text-4xl md:text-5xl font-black text-white tracking-tight">
          <span className="relative inline-block transform hover:scale-105 transition-transform duration-300">
            {/* Base metallic text with purple-tinted chrome and black */}
            <span className="text-transparent bg-clip-text bg-gradient-to-b from-purple-200 via-slate-600 via-black to-purple-950"
                  style={{
                    textShadow: `
                      0 1px 0 rgba(255,255,255,0.4),
                      0 2px 0 rgba(200,200,255,0.3),
                      0 3px 0 rgba(168,85,247,0.2),
                      0 4px 0 rgba(139,92,246,0.15),
                      0 5px 0 rgba(0,0,0,0.4),
                      0 8px 6px rgba(0,0,0,0.5),
                      0 12px 15px rgba(168,85,247,0.6),
                      0 0 30px rgba(168,85,247,0.5)
                    `,
                    WebkitTextStroke: '1.5px rgba(88,28,135,0.5)',
                    filter: 'brightness(1.3) contrast(1.2)'
                  }}>
              NoxTitan
            </span>
            {/* Top highlight shine */}
            <span className="absolute inset-0 text-transparent bg-clip-text bg-gradient-to-b from-white via-transparent to-transparent opacity-80"
                  style={{ WebkitTextStroke: '0px' }}>
              NoxTitan
            </span>
            {/* Animated glossy shine sweep */}
            <span className="absolute inset-0 bg-gradient-to-r from-transparent via-white to-transparent bg-clip-text text-transparent opacity-40"
                  style={{ 
                    backgroundSize: '200% 100%',
                    animation: 'shine 4s ease-in-out infinite',
                    WebkitTextStroke: '0px'
                  }}>
              NoxTitan
            </span>
          </span>
          <span className="hidden md:inline text-xs font-black px-2 py-1 rounded-md bg-gradient-to-br from-purple-300 via-slate-500 to-purple-900 text-white border-2 border-purple-400 ml-2 shadow-[0_4px_10px_rgba(168,85,247,0.6)]" 
                style={{ 
                  textShadow: '0 1px 0 rgba(255,255,255,0.3), 0 2px 3px rgba(0,0,0,0.8)',
                  boxShadow: 'inset 0 1px 0 rgba(255,255,255,0.3), 0 4px 10px rgba(168,85,247,0.6)'
                }}>
            ™
          </span>
        </Link>
      </div>
      <div className="hidden md:flex items-center gap-6 text-base font-semibold">
        <Link href="/calendar" className="hover:text-fuchsia-300 transition">Scheduler</Link>
        <Link href="/hr" className="hover:text-fuchsia-300 transition">HR</Link>
        <Link href="/payroll" className="hover:text-fuchsia-300 transition">Payroll</Link>
        <Link href="/compliance" className="hover:text-fuchsia-300 transition">Compliance</Link>
        <Link href="/accounting" className="hover:text-fuchsia-300 transition">Accounting</Link>
        <Link href="/ledger" className="hover:text-fuchsia-300 transition">General Ledger</Link>
        <Link href="/analytics" className="hover:text-fuchsia-300 transition">Analytics</Link>
      </div>
      <div className="flex items-center gap-2">
        <Link href="/demo-request" className="px-4 py-2 rounded-lg font-semibold bg-gradient-to-r from-purple-900 via-purple-700 to-black text-white shadow-lg hover:scale-105 transition border border-purple-500">Request Executive Demo</Link>
        <Link href="/vip" className="px-4 py-2 rounded-lg font-semibold bg-gradient-to-r from-purple-700 via-purple-500 to-purple-700 text-white border-2 border-purple-400 shadow-lg hover:scale-105 transition">VIP Access</Link>
        <Link href="/pricing" className="px-4 py-2 rounded-lg font-semibold bg-purple-700 text-white shadow-lg hover:scale-105 transition hover:bg-purple-800">Pricing</Link>
      </div>
      {/* Mobile menu button (optional for future) */}
    </nav>
  );
}
